import Vue from 'vue'
import Router from 'vue-router'
import Uploadsiniestros from '@/components/Uploadsiniestros'
import Uploadpolizas from '@/components/Uploadpolizas'
import Uploadvida from '@/components/Uploadvida'
import Index from '@/components/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/upload/siniestros',
      name: 'Uploadsiniestros',
      component: Uploadsiniestros
    },
    {
      path: '/upload/polizas',
      name: 'Uploadpolizas',
      component: Uploadpolizas
    },
    {
      path: '/upload/vida',
      name: 'Uploadvida',
      component: Uploadvida
    }
  ]
})



