<template>
<div style="margin-top: -60px;">
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container">
      <div class="navbar-header">
        <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar-collapse-01">
          <span class="sr-only">Toggle navigation</span>
        </button>
        <a class="navbar-brand" href="/#"><img src="static/img/logo.png" style="max-width:120px; margin-top: -12px;"></a>
      </div>
      <div id="navbar" class="collapse navbar-collapse">
        <ul class="nav navbar-nav">
          <li class="active">
            <a href="/"> Subida</a>
          </li>
          <li>
            <a href="edit"> Consulta</a>
          </li>
        </ul>

        <ul class="nav navbar-nav navbar-right">

          <li class="dropdown">
            <a href="#" class="dropdown-toggle" data-toggle="dropdown">
              <i class="fa fa-user"></i> Username
            </a>

          </li>

          <li>
            <form id="logout" name="logout" class="navbar-form pull-right" action="logout" method="post">
              <button class="btn btn-primary pull-right" type="submit">
                Log Out
              </button>
            </form>
          </li>


        </ul>

      </div>
    </div>
  </nav>
  <div class="container">
    <div class="row">
      <div class="col-lg-4">
        <div class="page-header">
          <h3>Subida Documentos <small>{{ uploadmode }}</small></h3>
        </div>
      </div>
    </div>
  </div>

  <div class="container" data-pg-collapsed>
      <div class="row">
        <div class="col-md-9 col-md-offset-1">
          <div class="text-left">
            <h3 class="text-left">Datos Siniestro</h3>
            <hr></hr>
            <div class="row">
              <div data-pg-collapsed class="col-md-2">
                <h4 class="text-left"><b>Poliza</b></h4>
              </div>
              <div data-pg-collapsed class="col-md-4">
                <h4 class="text-left">{{poliza}}</h4>
              </div>
              <div data-pg-collapsed class="col-md-2">
                <h4 class="text-left"><b>Riesgo</b></h4>
              </div>
              <div data-pg-collapsed class="col-md-4">
                <h4 class="text-left">{{riesgo}}</h4>
              </div>
            </div>
            <div class="row">
              <div data-pg-collapsed class="col-md-2">
                <h4 class="text-left"><b>Efecto</b></h4>
              </div>
              <div data-pg-collapsed class="col-md-4">
                <h4 class="text-left">{{fechaefecto}}</h4>
              </div>
              <div data-pg-collapsed class="col-md-2">
                <h4 class="text-left"><b>Vencimiento</b></h4>
              </div>
              <div data-pg-collapsed class="col-md-4">
                <h4 class="text-left">{{fechavencimiento}}</h4>
              </div>
            </div>
            <div class="row">
              <div data-pg-collapsed class="col-md-2">
                <h4 class="text-left"><b>Asegurado</b></h4>
              </div>
              <div data-pg-collapsed class="col-md-4">
                <h4 class="text-left">{{nombreasegurado}}</h4>
              </div>
              <div data-pg-collapsed class="col-md-2">
                <h4 class="text-left"><b>DNI</b></h4>
              </div>
              <div data-pg-collapsed class="col-md-4">
                <h4 class="text-left">{{dni}}</h4>
              </div>
              <br><br><hr>
            </div>
        </div>
      </div>
    </div> <!--row datos siniestro-->

    <div class="row">
      <div class="col-md-9 col-md-offset-1">
        <div class="text-left">
          <h3 class="text-left">Ficheros</h3>
          <span v-if="(fileList.length > 0)">
            <hr>
            <h4>Listado Añadidos</h4>
            <br>
          </span>
          <ul class="media-list" data-pg-collapsed>
            <li v-for="(file, index) in fileList" class="media">
              <a class="pull-left" href="#">
                <img class="media-object" src="https://placehold.it/100x100" width="100">
              </a>

              <div class="media-body">
                <h4 class="media-heading">{{ file.titulo }}</h4>
                {{ file.tipo }}
                <span class="pull-right">
                  <button class="btn btn-danger" v-on:click.stop="delFileFromList(index)"><i class=" fa fa-times"></i> Eliminar</button>
                </span>
              </div>
            </li>
          </ul>
          <br>
          <h4>Añadir Fichero</h4>
          <br>
          <div class="row">
            <div class="col-md-7">
              <select class="form-control" id="tipo-documental" v-model="tipodocumental" ref="tipo-documental" data-pg-collapsed>
                <option value="" selected disabled>Tipo Documental</option>
                <option>Condiciones Particulares</option>
                <option>Anulaciones/Rescates</option>
                <option>Otros</option>
              </select>
            </div>
            <div class="col-md-5">
              <input type="text" class="form-control" id="titulo" v-model="titulo" ref="titulo" placeholder="Titulo">
            </div>
          </div>

          <br><br>

          <div class="form-group">
            <div>
              <uploadbox></uploadbox>
            </div>
          </div>
          <div class="form-group">
            <div>
              <button type="submit" v-on:click.stop="addFileToList" class="btn btn-success pull-right"><i class="fa fa-plus"></i> Añadir a la lista</button>
            </div>
          </div>
        </div>
      </div>
    </div> <!--row ficheros-->
    <br><br>
    <hr>
    <div class="row">
      <div class="col-md-12">
        <span class="pull-right">
          <button class="btn btn-info btn-lg"><i class="fa fa-paper-plane"></i> Enviar Ficheros</button>
        </span>
      </div>
    </div>
    <br><br>

  </div>

</div>
</template>

<script>
  import Uploadbox from './Uploadbox'
  import store from '../store'
  import { eBus } from '../eventbus'

  export default {
    components: {Uploadbox},
    name: 'upload-siniestros',
    store,
    data () {
      return {
        uploadmode: 'polizas',
        poliza: '103393/0',
        riesgo: 'BI-0086-BT/VOLKSWAGEN',
        fechaefecto: '29/05/2008',
        fechavencimiento: '29/05/2018',
        nombreasegurado: 'VELASCO VEGAS, GLORIA',
        dni: '26171812C',
        tipodocumental: '',
        titulo: ''
      }
    },
    computed: {
      fileList () {
        return this.$store.getters.filesToUpload
      }
    },
    methods: {
      addFileToList: function () {
        store.commit('ADD_FILE_TO_LIST', {tipo: this.tipodocumental, titulo: this.titulo})
        this.tipodocumental = ''
        this.titulo = ''
        eBus.$emit('reset-uploadbox')
      },
      delFileFromList: function (index) {
        store.commit('DEL_FILE_FROM_LIST', index)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



