import Vue from 'vue'
import Vuex from 'vuex'
import VueResource from 'vue-resource'
import app from './App.vue'

Vue.use(Vuex)
Vue.use(VueResource)

export default new Vuex.Store({
  state: {
    filesToUpload: [],
    currentFile: {}
  },
  modules: {
    app
  },
  getters: {
    filesToUpload: state => state.filesToUpload
  },
  actions: {
    ADD_FILE ({ commit }) {
      commit('GET_USER')
    }
  },
  mutations: {
    updateApp (state, app) {
      if (app) {
        // state.groupId = app.groupId
        // state.artifactId = app.artifactId
        // nexus.versions(state.groupId, state.artifactId).then(response => {
        //   state.versions = response.data
        // })
      }
    },
    ADD_FILE_TO_LIST (state, data) {
      let newFile = {
        filename: state.currentFile.filename,
        filesize: state.currentFile.filesize,
        lastmodified: state.currentFile.lastModified,
        file: state.currentFile.file,
        tipo: data.tipo,
        titulo: data.titulo
      }
      state.filesToUpload.push(newFile)
    },
    DEL_FILE_FROM_LIST (state, index) {
      state.filesToUpload.splice(index, 1)
    },
    SET_FILE_INFO (state, data) {
      state.currentFile.filename = data.name
      state.currentFile.filesize = data.size
      state.currentFile.lastmodified = data.lastModifiedDate
      state.currentFile.file = data
    },
    UNSET_FILE_INFO (state) {
      state.currentFile = {}
    }
  }
})



