<template>
    <span>
      <div class="row">
        <div class="col-md-12">
          <div class="dropbox">
            <input type="file"
                   name="docfile"
                   id="docfile"
                   ref="docfile"
                   :disabled="isSelected"
                   @change="fileSelected($event.target.name, $event.target.files); fileCount = $event.target.files.length"
                   :accept="fileTypes"
                   class="input-file">

            <p v-if="isInitial">
                <i class="fa fa-cloud-upload fa-3x"></i>
                <br>Arrastra el Fichero o haz Click para Seleccionarlo
            </p>

            <p v-if="isSelected">
                <i class="fa fa-check-circle fa-3x"></i>
                <br>{{ fileInfo.name }}
                <br>{{ fileInfo.lastModifiedDate.toLocaleString() }}
                <br>{{ fileInfo.size | humanFileSize }}
            </p>

            <p v-if="isFailed">
                <i class="fa fa-times-circle fa-3x"></i>
                <br>Uploaded failed!<br>Please try again...
            </p>

            <p v-if="isSuccess">
                <i class="fa fa-check-circle fa-3x"></i>
                <br>Uploaded Successfully!
            </p>
        </div>
          <!--SUCCESS-->
        <div v-if="isSuccess">
            <h2>Uploaded {{ uploadedFiles.length }} file(s) successfully.</h2>
            <p>
                <a href="javascript:void(0)" @click="reset()">Upload again</a>
            </p>
            <ul class="list-unstyled">
                <li v-for="item in uploadedFiles">
                    <img :src="item.url" class="img-responsive img-thumbnail" :alt="item.originalName">
                </li>
            </ul>
        </div>
        </div>
      </div>
    </span>

</template>

<script>
    import Vue from 'vue'
    import store from '../store'
    import { eBus } from '../eventbus'

    Vue.filter('humanFileSize', require('../filters/humanFileSize'))

    const STATUS_INITIAL = 0
    const STATUS_SELECTED = 1
    const STATUS_SUCCESS = 2
    const STATUS_FAILED = 3

    export default {
      name: 'uploadbox',
      props: [],
      store,
      data () {
        return {
          fileTypes: '',
          uploadedFiles: [],
          uploadError: null,
          currentStatus: null,
          uploadFieldName: 'docfile',
          fileInfo: {}
        }
      },
      created: function () {
        eBus.$on('reset-uploadbox', this.reset)
      },
      computed: {
        now: function () {
          return Date.now()
        },
        btnSendDisabled: function () {
          return this.currentStatus === STATUS_INITIAL
        },
        isInitial () {
          return this.currentStatus === STATUS_INITIAL
        },
        isSelected () {
          return this.currentStatus === STATUS_SELECTED
        },
        isSuccess () {
          return this.currentStatus === STATUS_SUCCESS
        },
        isFailed () {
          return this.currentStatus === STATUS_FAILED
        }
      },
      methods: {
        reset () {
          this.uploadedFiles = []
          this.uploadError = null
          this.currentStatus = STATUS_INITIAL
          this.fileInfo = {}
          this.$store.commit('UNSET_FILE_INFO')
        },
        fileSelected (fieldName, fileList) {
          if (!fileList.length) return

          this.fileInfo.lastModifiedDate = fileList[0].lastModifiedDate
          this.fileInfo.name = fileList[0].name
          this.fileInfo.size = fileList[0].size
          this.fileInfo = fileList[0]

          this.currentStatus = STATUS_SELECTED
          this.$store.commit('SET_FILE_INFO', this.fileInfo)
        }
      },
      mounted () {
        this.reset()
      }
    }

</script>
<style scoped>
    .dropbox {
        outline: 2px dashed grey; /* the dash box */
        outline-offset: -10px;
        background: rgba(255, 255, 255, 1);
        color: dimgray;
        padding: 10px 10px;
        min-height: 200px; /* minimum height */
        position: relative;
        cursor: pointer;
    }

    .input-file {
        opacity: 0; /* invisible but it's there! */
        width: 100%;
        height: 200px;
        position: absolute;
        cursor: pointer;
    }

    .dropbox:hover {
        background: rgba(165, 18, 80, 0.1); /* when mouse over to the drop zone, change color */
        color: gray;
    }

    .dropbox p {
        font-size: 1.2em;
        text-align: center;
        padding: 50px 0;
    }
</style>



